export default {
    props: ['product'],

    methods: {
        addToCart() {
            this.$store.dispatch('addProductToCart', {
                product: this.product,
                quantity: 1
            });
        },

        addToWishList(){
            this.$store.dispatch('addProductToWishList', {
                product: this.product,
                quantity: 1
            })
        }
    }
}
